.App {
  font-family: sans-serif;
  text-align: center;
}

.the_app {
  vertical-align: middle;
  max-width: auto;
  margin-left: 10px;
  margin-right: 10px;
  background-color: #ffff;
  min-height: 500px;
  border: 1px solid #251d92;
  border-radius: 5px;
}

.form_container {
  max-width: 970px;
  margin: 0 auto;
  padding: 0.75rem;
  /*border-top: 1px solid #251d92;
      border-bottom: 2px solid #251d92;*/
}

.header {
  max-width: 970px;
  margin: 0 auto;
  padding: 0.75rem;
  border-top: 2px solid #251d92;
}

/*
styling for rendering description list like in eLabFTW. Credit to laura VS.
*/
dl {
  display: grid;
  grid-auto-columns: fit-content(300px) minmax(100px, 80vmax);
}
dt {
  grid-column: 1;
  padding: 2px 4px;
  background: #29aeb9;
  color: white;
  border: 0.5px solid #595959;
}

dd {
  grid-column: 2;
  margin: 0;
  padding: 2px 4px;
  background-color: #fff;
  border: 0.5px solid #595959;
}
